import React, { Component } from 'react'
import axios from 'axios'

export default class Search extends Component {
    handleSearch = async () => {
        const { keyWord: { value: data } } = this
        const { updateAppState } = this.props
        updateAppState({ isFirst: false, isLoading: true })
        await axios.get(`https://api.github.com/search/users?q=${data}`)
            .then((res) => {
                // console.log(res);
                // updateAppState(res.data.items)
                updateAppState({ isLoading: false, users: res.data.items })
            }, (rej) => {
                updateAppState({ isLoading: false, err: rej.message })
            })
    }
    render() {
        return (
            <div style={{ textAlign: 'center', marginBottom: '30px' }}>
                <h1>Search Github Users</h1>
                <input type="text" ref={c => this.keyWord = c} />
                <button onClick={this.handleSearch}>Search</button>
            </div>
        )
    }
}
